import React, {useContext} from 'react';
import { shallowEqual, useSelector, useDispatch } from 'react-redux'
import {LanguageContext} from "../../Providers/LanguageProvider";
import './forecastMobile.css'
import ForecastMobileDay from "./ForecastMobileDay";

export default () => {
  const { dictionary, language } = useContext(LanguageContext);

  const { forecasts} = useSelector(state => ({
    forecasts: state.weather.forecasts,
  }), shallowEqual);

  const forecastList = forecasts.map((item) => {
    console.dir(item);
    return (
      <ForecastMobileDay forecast={item} key={item.dt} dic={dictionary} language={language}/>
    );
  });

  return (
    <div className="fc_mobile_wrapper" data-testid="fc_mobile_wrapper">
      <h5 className="card-title ml-2">{dictionary.forecast.title}</h5>
      { forecastList }
    </div>
  );
}