import React from 'react';
import HumidityIcon from '../Icons/HumidityIcon';
import PressureIcon from '../Icons/PressureIcon';
import WindIcon from '../Icons/WindIcon';

import MapConditions from '../../Containers/CurrentWeather/MapConditionsToIcons';
import format from 'date-fns/format';

import { fromUnixTime } from 'date-fns'

export default ({ forecast, dic, language }) => {
  const { weatherDic, locale } = dic;

  const options = {
    locale: locale,
  };
  const fcDate = fromUnixTime(forecast.dt);

  let dayOfWeek = format(fcDate, "EE", options);
  dayOfWeek = dayOfWeek.charAt(0).toUpperCase() + dayOfWeek.slice(1);

  const dayFormated = format(fcDate, "dd.MM", options);
  const cond = MapConditions.get(forecast.condition);

  const condition = cond.condition[language.id];
  const iconColor = "#451600";

  return (
    <div className="card mob-card">
        <div>
          <span className="card-day card-text-color">{`${dayOfWeek}, ${dayFormated}`}</span>
        </div>
        <div className="card-center">
          <div className="card-center-info">
            <div>
              <span
                className="fc_max_temp card-text-color"
                data-testid="w_current_fact_temp"
              >
                {Math.round(forecast.temp_max)} &#176;
              </span>
              <span
                className="card-text-color ml-1"
                data-testid="w_current_fact_temp"
              >
                {Math.round(forecast.temp_min)} &#176;С
              </span>
            </div>
            <span className="card-cond text-left card-text-color">{condition}</span>
          </div>

          <div>
            <img src={cond.icon} width="56" height="56" alt="" />
          </div>

        </div>

        <div className="cond-items">
          <div className="cond-item">
            <HumidityIcon width={'24px'} height={'24px'} fill={iconColor} />
            <span className="card-text-color">{forecast.humidity}%</span>
          </div>
          <div className="cond-item">
            <PressureIcon width={'24px'} height={'24px'} fill={iconColor} />
            <span className="card-text-color">{forecast.pressure_mm}{weatherDic.pressure_um}</span>
          </div>
          <div className="cond-item">
            <WindIcon width={'24px'} height={'24px'} fill={iconColor} />
            <span className="card-text-color">{forecast.wind_speed} {weatherDic.wind_um}</span>
          </div>
        </div>

    </div>
  );
}